"use client";

import React, { useState, useEffect } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Commitment } from '@/types';

interface CommitmentModalProps {
    isOpen: boolean;
    onClose: () => void;
    commitmentToEdit?: Commitment | null;
}

export const CommitmentModal = ({ isOpen, onClose, commitmentToEdit }: CommitmentModalProps) => {
    const {
        addCommitment,
        updateCommitment,
        accounts,
        categories,
        ledgers,
        activeBookId
    } = useFinance();

    const [name, setName] = useState('');
    const [amount, setAmount] = useState('');
    const [frequency, setFrequency] = useState<'MONTHLY' | 'WEEKLY' | 'ANNUAL' | 'DAILY' | 'BIWEEKLY' | 'QUARTERLY' | 'SEMIANNUAL'>('MONTHLY');
    const [type, setType] = useState<'FIXED' | 'TEMPORAL'>('FIXED');
    const [status, setStatus] = useState<'PENDING' | 'PAID' | 'LATE' | 'SKIPPED'>('PENDING');
    const [nextDueDate, setNextDueDate] = useState('');
    const [endDate, setEndDate] = useState(''); // Optional
    const [categoryId, setCategoryId] = useState('');
    const [accountId, setAccountId] = useState('');
    const [isActive, setIsActive] = useState(true);

    // Populate form on open
    useEffect(() => {
        if (isOpen) {
            if (commitmentToEdit) {
                setName(commitmentToEdit.name);
                setAmount(commitmentToEdit.amount.toString());
                setFrequency(commitmentToEdit.frequency);
                setType(commitmentToEdit.type || 'FIXED');
                setStatus(commitmentToEdit.status || 'PENDING');

                // Format date for input type="date"
                const dueDate = commitmentToEdit.nextDueDate ? new Date(commitmentToEdit.nextDueDate).toISOString().split('T')[0] : '';
                setNextDueDate(dueDate);

                const endD = commitmentToEdit.endDate ? new Date(commitmentToEdit.endDate).toISOString().split('T')[0] : '';
                setEndDate(endD);

                setCategoryId(commitmentToEdit.categoryId || categories.find(c => c.name === commitmentToEdit.category)?.id || '');
                setAccountId(commitmentToEdit.accountId || accounts.find(a => a.name === commitmentToEdit.account)?.id || '');
                setIsActive(commitmentToEdit.isActive ?? true);
            } else {
                // Reset for new
                setName('');
                setAmount('');
                setFrequency('MONTHLY');
                setType('FIXED');
                setStatus('PENDING');
                setNextDueDate(new Date().toISOString().split('T')[0]);
                setEndDate('');
                setCategoryId('');
                setAccountId(accounts[0]?.id || '');
                setIsActive(true);
            }
        }
    }, [isOpen, commitmentToEdit, accounts, categories]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        const numAmount = parseFloat(amount);
        if (isNaN(numAmount) || numAmount <= 0) {
            alert('Por favor ingresa un monto válido');
            return;
        }

        if (!name.trim()) {
            alert('Por favor ingresa un nombre para el compromiso');
            return;
        }

        const data: any = {
            name,
            amount: numAmount,
            frequency,
            type,
            status,
            nextDueDate: new Date(nextDueDate).toISOString(),
            isActive,
            categoryId: categoryId || undefined,
            accountId: accountId || undefined,
            category: categories.find(c => c.id === categoryId)?.name,
            account: accounts.find(a => a.id === accountId)?.name
        };

        if (endDate) {
            data.endDate = new Date(endDate).toISOString();
        }

        try {
            if (commitmentToEdit) {
                await updateCommitment(commitmentToEdit.id, data);
            } else {
                await addCommitment(data);
            }
            onClose();
        } catch (error) {
            console.error("Error saving commitment:", error);
        }
    };

    // Close on Escape
    useEffect(() => {
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.key === 'Escape' && isOpen) onClose();
        };
        window.addEventListener('keydown', handleKeyDown);
        return () => window.removeEventListener('keydown', handleKeyDown);
    }, [isOpen, onClose]);

    if (!isOpen) return null;

    const currencySymbol = ledgers.find(l => l.id === activeBookId)?.currency || '$';

    return (
        <div
            className="fixed top-0 bottom-0 right-0 left-0 lg:left-[var(--sidebar-width)] z-50 flex items-center justify-center p-4 bg-black/60 backdrop-blur-sm animate-fade-in transition-[left] duration-300"
            onClick={onClose}
        >
            <div
                className="bg-white dark:bg-[#1e2530] w-full max-w-2xl rounded-3xl shadow-2xl overflow-hidden flex flex-col max-h-[90vh]"
                onClick={(e) => e.stopPropagation()}
            >
                {/* Header */}
                <div className="px-8 py-6 border-b border-gray-100 dark:border-white/5 flex justify-between items-center">
                    <h2 className="text-2xl font-bold text-gray-800 dark:text-white">
                        {commitmentToEdit ? 'Editar Compromiso' : 'Nuevo Compromiso'}
                    </h2>
                    <button onClick={onClose} className="p-2 hover:bg-gray-100 dark:hover:bg-white/5 rounded-full transition-colors">
                        <span className="material-icons text-gray-500">close</span>
                    </button>
                </div>

                <form onSubmit={handleSubmit} className="flex-1 overflow-y-auto p-8">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">

                        {/* Name */}
                        <div className="col-span-2">
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Nombre</label>
                            <input
                                type="text"
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white"
                                placeholder="Ej. Netflix, Renta, Gimnasio"
                                autoFocus
                            />
                        </div>

                        {/* Amount */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Monto ({currencySymbol})</label>
                            <input
                                type="number"
                                value={amount}
                                onChange={(e) => setAmount(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white font-mono"
                                placeholder="0.00"
                            />
                        </div>

                        {/* Frequency */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Frecuencia</label>
                            <select
                                value={frequency}
                                onChange={(e: any) => setFrequency(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white appearance-none cursor-pointer"
                            >
                                <option value="WEEKLY">Semanal</option>
                                <option value="BIWEEKLY">Quincenal</option>
                                <option value="MONTHLY">Mensual</option>
                                <option value="QUARTERLY">Trimestral</option>
                                <option value="SEMIANNUAL">Semestral</option>
                                <option value="ANNUAL">Anual</option>
                            </select>
                        </div>

                        {/* Type (Fixed/Temporal) */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Tipo</label>
                            <div className="flex bg-gray-50 dark:bg-[#252b36] p-1 rounded-xl border border-gray-200 dark:border-gray-700">
                                <button
                                    type="button"
                                    onClick={() => setType('FIXED')}
                                    className={`flex-1 py-2 rounded-lg text-sm font-bold transition-all ${type === 'FIXED' ? 'bg-white dark:bg-[#1e2530] shadow-sm text-primary' : 'text-gray-400 hover:text-gray-600'}`}
                                >
                                    Fijo
                                </button>
                                <button
                                    type="button"
                                    onClick={() => setType('TEMPORAL')}
                                    className={`flex-1 py-2 rounded-lg text-sm font-bold transition-all ${type === 'TEMPORAL' ? 'bg-white dark:bg-[#1e2530] shadow-sm text-amber-500' : 'text-gray-400 hover:text-gray-600'}`}
                                >
                                    Temporal
                                </button>
                            </div>
                        </div>

                        {/* Status */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Estado Inicial</label>
                            <select
                                value={status}
                                onChange={(e: any) => setStatus(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white appearance-none cursor-pointer"
                            >
                                <option value="PENDING">Pendiente</option>
                                <option value="PAID">Pagado</option>
                                <option value="SKIPPED">Omitido</option>
                            </select>
                        </div>

                        {/* Next Due Date */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Próximo Vencimiento</label>
                            <input
                                type="date"
                                value={nextDueDate}
                                onChange={(e) => setNextDueDate(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white"
                            />
                        </div>

                        {/* End Date (Optional) */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Finaliza (Opcional)</label>
                            <input
                                type="date"
                                value={endDate}
                                onChange={(e) => setEndDate(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white"
                            />
                        </div>

                        {/* Category */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Categoría</label>
                            <select
                                value={categoryId}
                                onChange={(e) => setCategoryId(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white appearance-none cursor-pointer"
                            >
                                <option value="">Seleccionar Categoría</option>
                                {categories.map(cat => (
                                    <option key={cat.id} value={cat.id}>{cat.name}</option>
                                ))}
                            </select>
                        </div>

                        {/* Account */}
                        <div>
                            <label className="block text-xs font-bold text-gray-400 uppercase tracking-widest mb-2">Cuenta de Pago</label>
                            <select
                                value={accountId}
                                onChange={(e) => setAccountId(e.target.value)}
                                className="w-full bg-gray-50 dark:bg-[#252b36] border border-gray-200 dark:border-gray-700 rounded-xl px-4 py-3 outline-none focus:ring-2 focus:ring-primary/50 transition-all dark:text-white appearance-none cursor-pointer"
                            >
                                <option value="">Seleccionar Cuenta</option>
                                {accounts.map(acc => (
                                    <option key={acc.id} value={acc.id}>{acc.name}</option>
                                ))}
                            </select>
                        </div>

                    </div>
                </form>

                {/* Footer buttons */}
                <div className="p-6 bg-gray-50 dark:bg-[#252b36] border-t border-gray-100 dark:border-white/5 flex justify-end gap-4">
                    <button
                        type="button"
                        onClick={onClose}
                        className="px-6 py-3 rounded-xl font-bold text-gray-500 hover:text-gray-800 dark:text-gray-400 dark:hover:text-white transition-colors"
                    >
                        Cancelar
                    </button>
                    <button
                        onClick={handleSubmit}
                        className="px-8 py-3 rounded-xl font-bold bg-primary text-white shadow-lg shadow-primary/30 hover:bg-primary/90 hover:shadow-primary/40 transition-all transform hover:-translate-y-0.5 active:translate-y-0"
                    >
                        {commitmentToEdit ? 'Guardar Cambios' : 'Crear Compromiso'}
                    </button>
                </div>
            </div>
        </div>
    );
};
